import axios from "../http"

export default {
  // Request http de Login do Usurio
  login (userData) {
    return axios.post("api/auth", userData)
  },
  // Request http de Registrar Usurio
  register (userData) {
    return axios.post("api/register", userData)
  },
  // Request de http de Validao do usurio
  loadSession () {
    return axios.post("api/loadSession")
  }
}
